import React from "react"
import { FormOne, FormTwo } from './forms'


export default class Layout extends React.Component {
	constructor(props) {
		super(props);
		this.handleForm1Click = this.handleForm1Click.bind(this);
		this.handleForm2Click = this.handleForm2Click.bind(this);
		this.state = { form1: true };
	}

	handleForm1Click() {
		this.setState({ form1: true })
	}

	handleForm2Click() {
		this.setState({ form1: false })
	}

	handelSubmitted() {
		this.setState({ submitted: true })
	}

	render() {
		return (
			<div className="container">
				<div className="heading text-center my-5">
					<h1 style={{ fontSize: `2rem` }}>Hi there!<br />This is the demo of submiting react forms to google sheets</h1>
				</div>
				<div className="btn-group mb-5" style={{ borderRadius: `25rem`, backgroundColor: `#ececec`, padding: `0.25rem` }}>
					<a onClick={this.handleForm1Click} class="btn" style={{ borderRadius: `25rem`, backgroundColor: `${this.state.form1 ? `#ffffff` : `transparent`}`, padding: `0.5rem 1.75rem`, fontWeight: `600`, color: `#1b1b1b` }}>Form 1</a>
					<a onClick={this.handleForm2Click} class="btn" style={{ borderRadius: `25rem`, backgroundColor: `${!this.state.form1 ? `#ffffff` : `transparent`}`, padding: `0.5rem 1.75rem`, fontWeight: `600`, color: `#4f4f4f` }}>Form 2</a>
				</div>
				<div className="card mx-auto px-2 px-md-4 py-4 mb-5" style={{ maxWidth: `22rem` }}>
					{this.state.form1 ? <FormOne></FormOne> : <FormTwo></FormTwo>}
				</div>
				<div class="modal fade" id="submissionModal" tabindex="-1" aria-labelledby="submissionModalLabel" aria-hidden="true">
					<div class="modal-dialog">
						<div class="modal-content">
							<div class="modal-header">
								<h5 class="modal-title" id="exampleModalLabel">Google Sheet</h5>
								<button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
							</div>
							<div class="modal-body">
							<iframe src="https://docs.google.com/spreadsheets/d/e/2PACX-1vRJNN8NPG5SVZska1gcy6DCWj3fmhTzP0wb_tZ962-etR-4fa4dD9ir9dS9WFGB1M8I0TL-Mv3H7i6v/pubhtml?widget=true&amp;headers=false" width="100%" height="250px"></iframe>
      						</div>
							<div class="modal-footer justify-content-flex-start">
								<p className="text-muted" style={{fontSize: `0.75rem`}}>Your entries will be deleted automatically!</p>
							</div>
						</div>
					</div>
				</div>
				<footer className="footer-navbar navbar">
					<div className="navbar-nav flex-column flex-md-row">
						<li className="nav-item me-3"><a className="nav-link" href="https://github.com/DhruvMittal1806/React-Form-To-Google-Sheets">GitHub Repo</a></li>
						<li className="nav-item mx-3"><a className="nav-link" href="https://github.com/DhruvMittal1806/React-Form-To-Google-Sheets">Deployed on GitHub Pages</a></li>
						<li className="nav-item mx-3"><a className="nav-link" href="https://github.com/DhruvMittal1806/React-Form-To-Google-Sheets">Learn React</a></li>
						<li className="nav-item mx-3"><a className="nav-link" href="https://github.com/DhruvMittal1806/React-Form-To-Google-Sheets">Learn about Google Script</a></li>
					</div>
				</footer>
				<style jsx>{
					`
					.footer-navbar {
						border-top: 0.75px solid #cecece;
						margin-top: 5rem
					}
					.footer-navbar .nav-item .nav-link {
						color: #1a1a1a;
						font-size: 0.75rem;
					}
					`
				}</style>
			</div>
		)
	}
}