import { React, useState } from "react";

export function FormOne() {
	const scriptURL = 'https://script.google.com/macros/s/AKfycbyEIHhu2i4lEcL3q5xNHhKsqT5mDDfIPR9V0znv6y9E0VciW2sJT-N75eu0cKdXyaJYlA/exec'

	const [status, setStatus] = useState("Submit");
	const handleSubmit = async (e) => {
		e.preventDefault();
		setStatus("Sending...");

		let formData = new FormData();    //formdata object

		const { favouriteBook, marsThoughts } = e.target.elements;
		let details = {
			"Favourite Book" : favouriteBook.value,
			"What are your thoughts on Mars" : marsThoughts.value,
			sheetName: "Form1"
		};

		for (var key in details) {
			formData.append(key, details[key])
		}

		fetch(scriptURL, { method: 'POST', body: formData })
			.then(response => {
				console.log('Success!', response)
				document.querySelector(".contact-form-success").classList.toggle("d-none")
				document.querySelector(".custom-form").classList.toggle("d-none")
				setStatus("Submited");
			})
			.catch(error => {
				console.error('Error!', error.message)
				setStatus("Retry");
			})


	};

	return (
		<div>
			<form className="custom-form" name="submit-to-google-sheet" onSubmit={handleSubmit}>
				{/* <input value="Form1" className="d-none" id="form1" name="form1"/> */}
				<div className="row">
					<div className="col-12">
						<div className="form-group text-start mb-4">
							<label for="favouriteBook" className="form-label">What's your favourite Book ?</label>
							<input
								type="text"
								className="form-control"
								name="Favourite Book"
								id="favouriteBook"
								placeholder="The hitchhiker's guide to galaxy"
								required
							/>
						</div>
					</div>
					<div className="col-12">
						<div className="form-group text-start mb-4">
							<label for="marsThoughts" className="form-label">What are your thoughts on Mars ?</label>
							<input
								type="text"
								className="form-control"
								name="What are your thoughts on Mars"
								placeholder="Can't wait to go there"
								id="marsThoughts"
								required
							/>
						</div>
					</div>
					<div className="col-12">
						<button className="btn btn-dark w-100" type="submit">{status}</button>
					</div>
				</div>
			</form>
			<div className="contact-form-success text-center d-none">
				<img style={{ width: '100%', borderRadius: `0.25rem` }} className="mb-4" src="/images/1.gif" />
				<a className="btn btn-outline-dark w-100" data-bs-toggle="modal" data-bs-target="#submissionModal">Check your entry</a>
			</div>
		</div>
	)
}

